
import Balance from '@/components/Balance.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'

import Clipboard from 'clipboard'

export default {
components: {
XHeader, Group, Balance, XButton, Box, Qrcode
},
data () {
return {
headerTitle: '接收游戏金',
gameGold: '游戏金',
address: '',
number: 0.0
}
},
methods: {
onBack() {
this.$router.push('/wallet')
},
copyAddr(text, event){
const clipboard = new Clipboard(event.target, {
text: () => text
})
clipboard.on('success', e => {

this.$vux.toast.show({text: '已复制到剪贴板'})
clipboard.destroy()
})
clipboard.on('error', e => {


this.$vux.toast.show({text: '浏览器不支持复制'})
clipboard.destroy()
})
},

},
created() {
console.log('this.GLOBAL.userProfile', this.GLOBAL.userProfile)
if(this.GLOBAL.userProfile == null) {
this.$router.push('/mine')
} else {
this.address = this.GLOBAL.userProfile.block_addr
}
}
}



