
import Balance from '@/components/Balance.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Group from 'vux/src/components/group/index.vue'


const getItems = () => [{
label: '发送',
tag: 'send',
index: 0,
icon: 'static/img/icon1/7.png',
link: '/wallet/send'
}, {
label: '接收',
tag: 'receive',
index: 1,
icon: 'static/img/icon1/1.png',
link: '/wallet/receive'
},{
label: '交易流水',
tag: 'detail',
index: 2,
icon: 'static/img/icon1/3.png',
link: '/wallet/detail'
},{
label: '发布BTC交易对',
tag: 'trans',
index: 3,
icon: 'static/img/icon1/5.png',
link: '/wallet/trans'
}]

export default {
components: {
XHeader, Balance, Grid, GridItem, Group
},
data () {
return {
headerTitle: '我的钱包',
items: getItems()
}
},
methods: {
onBack() {
this.$router.push('/mine')
},
onItemClick() {

}
},
created() {

}
}



