

import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
name: 'ContractInfo',
components: {
XHeader, XButton, Flexbox, FlexboxItem, Group
},
data () {
return {
headerTitle: '道具交易详情',
propSale: null
}
},
methods: {

onBack() {

this.$router.go(-1);
},
showPlugin(msg) {
this.$vux.alert.show({
title: '提示',
content: msg
})
},

showPluginAuto(msg) {
this.showPlugin(msg)
setTimeout(() => {
this.$vux.alert.hide()
}, 2000)
},
propBuyConfirm() {
let that = this
this.$vux.confirm.show({
title: '操作提示',
content: '确定购买这个道具?',
onConfirm () {
that.propBuy()
}
})
},
propBuy() {
let data = {
func:'PropBuy',
control: 'prop',
oemInfo: this.GLOBAL.oemInfo,
pid: this.propSale.pid,
price: this.propSale.price,
openid: this.GLOBAL.openid
};
this.axios.post(this.GLOBAL.apiUrl, data).then(res => {
console.log(res.data);
if(res.data.errcode='success') {
this.showPluginAuto('道具交易成功!')
this.$router.go(-1);
} else {
this.showPluginAuto('道具交易失败!')
}
}).catch(res => {
console.log(res);
});
}

},
created() {
if(!!this.$route.params.propSale) {
this.propSale = this.$route.params.propSale
}
}
}



