

import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
name: 'ContractInfo',
components: {
XHeader, XButton, Flexbox, FlexboxItem, Group
},
data () {
return {
headerTitle: '交易对详情',
contract: null
}
},
methods: {

onBack() {

this.$router.go(-1);
},
showPlugin(msg) {
this.$vux.alert.show({
title: '提示',
content: msg
})
},

showPluginAuto(msg) {
this.showPlugin(msg)
setTimeout(() => {
this.$vux.alert.hide()
}, 2000)
},
contractConfirm() {
let that = this
this.$vux.confirm.show({
title: '操作提示',
content: '确定签署该笔交易?',
onConfirm () {
that.contractPromise()
}
})
},
contractPromise() {
let data = {
func:'ContractPromise',
control: 'contract',
oemInfo: this.GLOBAL.oemInfo,
txid: this.contract.current.hash,
openid: this.GLOBAL.openid
};
this.axios.post(this.GLOBAL.apiUrl, data).then(res => {
console.log(res.data);
if(res.data.errcode='success') {
this.showPluginAuto('该笔交易对签署成功!')
this.$router.go(-1);
} else {
this.showPluginAuto('该笔交易对签署失败!')
}
}).catch(res => {
console.log(res);
});
}

},
created() {
if(!!this.$route.params.contract) {
this.contract = this.$route.params.contract
}
}
}



