
import Card from 'vux/src/components/card/index.vue'
import Countup from 'vux/src/components/countup/index.vue'


export default {
components: {
Card, Countup
},
data () {
return {
gameGold: '游戏金',
balance: {
confirmed: 0,
unconfirmed: 0
},
doStart: false
}
},
methods: {

balanceAll() {
let data = {func:'BalanceAll', control: 'wallet', openid: this.GLOBAL.openid, oemInfo: this.GLOBAL.oemInfo}
this.axios.post(this.GLOBAL.apiUrl, data).then(res => {
this.balance.confirmed = this.GLOBAL.formatGameGold(res.data.balance.confirmed)
this.balance.unconfirmed = this.GLOBAL.formatGameGold(res.data.balance.unconfirmed-res.data.balance.confirmed)

this.doStart = true
}).catch(res => {
console.log(res)
})
},
getConfirmed() {
return this.balance.confirmed
},
getUnConfirmed() {
return this.balance.unconfirmed
}
},
created() {
this.balanceAll()
}
}



