
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'


export default {
name: 'MemberDrawLog',
components: {
XHeader, Flexbox, FlexboxItem, Group
},
props: [
'mine'
],
data () {
return {
headerTitle: '全部提取记录',
title: '',
drawCount: 0,
drawLog: [],
}
},
methods: {
onBack() {
this.$router.go(-1)
},
getDrawLog() {
let data = {
func:'VipDrawLog',
control: 'profile',
uid: this.GLOBAL.uid,
last: 0,
oemInfo: this.GLOBAL.oemInfo
};
this.axios.post(this.GLOBAL.apiUrl, data).then(res => {
console.log(res.data);
if(res.data.errcode='success') {
this.drawLog = res.data.ret
res.data.ret.forEach(element => {
this.drawCount += element.draw_count
});
this.title = '累计提取' + this.drawLog.length + '次，共' + this.GLOBAL.formatGameGold(this.drawCount) + '千克'
}
});
}
},
created() {
if(this.GLOBAL.userProfile == null) {
this.$router.push('/mine')
}
this.getDrawLog()
}
}



