

import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
name: 'Contract',
components: {
Flexbox, FlexboxItem
},
data () {
return {
contractList:[]
}
},
methods: {

getContractList() {
let data = {func:'ContractList', control: 'contract', oemInfo: this.GLOBAL.oemInfo};
this.axios.post(this.GLOBAL.apiUrl, data).then(res => {
console.log(res.data);
this.contractList = res.data.ret;
}).catch(res => {
console.log(res);
})
},
contracInfo(item, index) {
this.$router.push({ name: 'ContractInfo', params: { contract: item }})
}
},
created() {
this.getContractList();
}
}



