
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
name: 'MemberGold',
directives: {
TransferDom
},
components: {
Flexbox, FlexboxItem, Group, Countup, XCircle
},
props: [
'mine'
],
data () {
return {
doStart: false,
gold: 1000,
percent: 0,
drawLog: [],
timer: null,
canDrawGold: 10,
maxPercent: 0,
timer2: null,
}
},
mounted() {
console.log('mounted')

},

beforeDestroy: function () {
console.log('beforeDestroy')
clearInterval(this.timer)
clearInterval(this.timer2)
},

created() {
console.log('created')
let current_time = parseInt(new Date().getTime() / 1000)
this.GLOBAL.vipGetNotifyTime = current_time
if(this.mine.vip_usable_count > 0) {
this.gold = this.GLOBAL.formatGameGold(this.mine.vip_usable_count);
this.doStart = true
this.getDrawLog()
this.doCircle()

}
},

methods: {
doCircle() {
this.maxPercent = parseInt(this.gold / this.canDrawGold * 100)
let interval = 50
if(this.maxPercent > 100) {
this.maxPercent = 100
interval = 10
}
console.log('this.maxPercent', this.maxPercent)
this.percent = 0
this.setTimer(interval)
},
setTimer: function (val) {
console.log('setTimer')
this.timer = setInterval( () => {
this.percent += 5
if(this.percent >= this.maxPercent) {
clearInterval(this.timer)
}
}, val);
},
setTimer2: function () {
console.log('setTimer')
this.timer2 = setInterval( () => {
this.gold += 0.0011
this.percent++
}, 1000);
},
getVipImg(vip_level) {
return 'static/img/member/vip_'+vip_level+'.png'
},

allDrawLog() {
this.$router.push({name: 'MemberDrawLog'})
},

vipDrawConfirm() {
let that = this
this.$vux.confirm.show({
title: '输入提取游戏金数量(千克)',
content: '输入提取游戏金数量(千克)',
showInput: true,
onConfirm (value) {
if(that.checkRate(value)==false) {
that.showPluginAuto('输入无效数字')
} else {
let draw_count = that.GLOBAL.gameGoldOrigin(value)
if(value < 10) {
that.showPluginAuto('提取数量不能少于10千克')
} else if(draw_count > that.mine.vip_usable_count) {
that.showPluginAuto('提取数量超出额度')
} else {
that.vipDraw(draw_count)
}
}
}
})
},

vipDraw(draw_count) {
let data = {
func:'VipDraw',
control: 'profile',
uid: this.GLOBAL.uid,
draw_count: draw_count,
oemInfo: this.GLOBAL.oemInfo
};
this.axios.post(this.GLOBAL.apiUrl, data).then(res => {
console.log('vipDraw', res.data);
if(res.data.errcode='success') {
this.percent = 0
this.doStart = false
this.mine.vip_usable_count = this.mine.vip_usable_count - draw_count
this.gold = this.GLOBAL.formatGameGold(this.mine.vip_usable_count )
this.doStart = true
this.drawLog.unshift(res.data.ret)
this.showPluginAuto('提币成功')
this.doCircle()
}
});
},

getDrawLog() {
let data = {
func:'VipDrawLog',
control: 'profile',
uid: this.GLOBAL.uid,
last: 1,
oemInfo: this.GLOBAL.oemInfo
};
this.axios.post(this.GLOBAL.apiUrl, data).then(res => {
console.log(res.data);
if(res.data.errcode='success') {
this.drawLog = res.data.ret
}
});
},
checkRate(input) {
var re = /^[0-9]+.?[0-9]*$/; 
return re.test(input)
},
showPlugin(msg) {
this.$vux.alert.show({
title: '提示',
content: msg
})
},

showPluginAuto(msg) {
this.showPlugin(msg)
setTimeout(() => {
this.$vux.alert.hide()
}, 3000)
},
}
}



